@font-face {
  font-family: Digital;
  src: url(/simon-says-react-redux/static/media/digital-subset.55ebeeb7.ttf);
}

@font-face {
  font-family: "Lilita One";
  src: url(/simon-says-react-redux/static/media/LilitaOne-Regular.0d4e1383.ttf);
}

@-webkit-keyframes slideInFromLeft {
  0% {
    opacity: 0.01;
    -webkit-transform: rotate(-360deg) translateX(-100%);
            transform: rotate(-360deg) translateX(-100%);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) translate(0%);
            transform: rotate(0deg) translate(0%);
    transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: transform 0.3s, opacity 0.3s;
    transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  }
}

@keyframes slideInFromLeft {
  0% {
    opacity: 0.01;
    -webkit-transform: rotate(-360deg) translateX(-100%);
            transform: rotate(-360deg) translateX(-100%);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) translate(0%);
            transform: rotate(0deg) translate(0%);
    transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: transform 0.3s, opacity 0.3s;
    transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #34495e;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container {
  height: 75vh;
  width: 75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  background-color: #2c3e50;
  position: relative;
  margin: 10vh auto;
  padding: 2vh;
  overflow: hidden;
  border-radius: 50%;
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
          animation: 1s ease-out 0s 1 slideInFromLeft;
}

.button {
  height: 33.8vh;
  width: 33.5vh;
  box-sizing: border-box;
  margin: 1vh;
  display: inline-block;
  overflow: hidden;
}
.button.shrink {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}

.button:hover .on {
  cursor: pointer;
}

.overlay {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.15s;
}

.overlay.on {
  background-image: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.4) 1%,
    rgba(255, 255, 255, 0.1) 70%,
    rgba(255, 255, 255, 0.05) 100%
  );
  opacity: 1;
}

.button:nth-of-type(1) {
  border-radius: 500% 0 0 0;
  background-color: #e74c3c;
  background-image: radial-gradient(
    circle at bottom right,
    rgba(0, 0, 0, 0) 43%,
    rgba(0, 0, 0, 0.1) 43.1%,
    rgba(0, 0, 0, 0.1) 45%,
    rgba(0, 0, 0, 0) 45.1%,
    rgba(0, 0, 0, 0) 53%,
    rgba(0, 0, 0, 0.1) 53.1%,
    rgba(0, 0, 0, 0.1) 55%,
    rgba(0, 0, 0, 0) 55.1%,
    rgba(0, 0, 0, 0) 63%,
    rgba(0, 0, 0, 0.1) 63.1%,
    rgba(0, 0, 0, 0.1) 65%,
    rgba(0, 0, 0, 0) 65.1%
  );
}

.button:nth-of-type(2) {
  border-radius: 0 500% 0 0;
  background-color: #f1c40f;
  background-image: radial-gradient(
    circle at bottom left,
    rgba(0, 0, 0, 0) 43%,
    rgba(0, 0, 0, 0.1) 43.1%,
    rgba(0, 0, 0, 0.1) 45%,
    rgba(0, 0, 0, 0) 45.1%,
    rgba(0, 0, 0, 0) 53%,
    rgba(0, 0, 0, 0.1) 53.1%,
    rgba(0, 0, 0, 0.1) 55%,
    rgba(0, 0, 0, 0) 55.1%,
    rgba(0, 0, 0, 0) 63%,
    rgba(0, 0, 0, 0.1) 63.1%,
    rgba(0, 0, 0, 0.1) 65%,
    rgba(0, 0, 0, 0) 65.1%
  );
}

.button:nth-of-type(3) {
  border-radius: 0 0 0 500%;
  background-color: #2ecc71;
  background-image: radial-gradient(
    circle at top right,
    rgba(0, 0, 0, 0) 43%,
    rgba(0, 0, 0, 0.1) 43.1%,
    rgba(0, 0, 0, 0.1) 45%,
    rgba(0, 0, 0, 0) 45.1%,
    rgba(0, 0, 0, 0) 53%,
    rgba(0, 0, 0, 0.1) 53.1%,
    rgba(0, 0, 0, 0.1) 55%,
    rgba(0, 0, 0, 0) 55.1%,
    rgba(0, 0, 0, 0) 63%,
    rgba(0, 0, 0, 0.1) 63.1%,
    rgba(0, 0, 0, 0.1) 65%,
    rgba(0, 0, 0, 0) 65.1%
  );
}

.button:nth-of-type(4) {
  border-radius: 0 0 500% 0;
  background-color: #9b59b6;
  background-image: radial-gradient(
    circle at top left,
    rgba(0, 0, 0, 0) 43%,
    rgba(0, 0, 0, 0.1) 43.1%,
    rgba(0, 0, 0, 0.1) 45%,
    rgba(0, 0, 0, 0) 45.1%,
    rgba(0, 0, 0, 0) 53%,
    rgba(0, 0, 0, 0.1) 53.1%,
    rgba(0, 0, 0, 0.1) 55%,
    rgba(0, 0, 0, 0) 55.1%,
    rgba(0, 0, 0, 0) 63%,
    rgba(0, 0, 0, 0.1) 63.1%,
    rgba(0, 0, 0, 0.1) 65%,
    rgba(0, 0, 0, 0) 65.1%
  );
}

.controls {
  height: 50%;
  width: 50%;
  border: 2vh solid #2c3e50;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  margin: -25% 0 0 -25%;
  background-color: white;
  z-index: 1;
  overflow: hidden;
  box-shadow: inset 0 0.7vh 1.4vh rgba(0, 0, 0, 0.45),
    inset 0 0.5vh 0.5vh rgba(0, 0, 0, 0.42);
}

.controls * {
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.control-buttons {
  height: 100%;
  width: 100%;
  position: relative;
}

.game-light {
  height: 20%;
  width: 10%;
  background-color: #16a085;
  border: 0.33vh solid #2c3e50;
  border-radius: 50%;
  position: absolute;
  left: 65%;
  top: 55%;
}

.game-controls {
  height: 48%;
  width: 100%;
  border-top: 0.5vh solid #2c3e50;
  position: absolute;
  bottom: 0;
  left: 0;
  text-align: center;
  border-radius: 0 0 500px 500px;
  background-color: #ecf0f1;
}

.game-reset {
  height: 20%;
  width: 10%;
  background-color: #c0392b;
  border-radius: 50%;
  border: 0.33vh solid #2c3e50;
  position: absolute;
  left: 25%;
  top: 55%;
  cursor: pointer;
}

.game-name {
  height: 100%;
  width: 100%;
  z-index: -1;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  align-items: center;
  background-image: radial-gradient(
    #bdc3c7 65%,
    #2c3e50 65.01%,
    #2c3e50 68%,
    #bdc3c7 68.01%
  );
}
.game-name span.name {
  font-size: 7vh;
  margin-top: 2vh;
  position: absolute;
  top: 15%;
  color: #2c3e50;
  font-family: "Lilita One", monospace;
  text-transform: uppercase;
}
.game-name span.game-name-buffer {
  width: 100%;
  background-color: #bdc3c7;
  height: 8%;
}
.game-switch {
  height: 20%;
  width: 20%;
  cursor: pointer;
  background-color: #2c3e50;
  position: absolute;
  top: 60%;
  left: 40%;
  border-radius: 5px;
}
.game-switch * {
  overflow: visible;
}
.game-switch div {
  display: block;
}
.game-switch span {
  display: none;
}
.game-switch div.game-switch-toggle {
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
  border-radius: 2px;
  background: #bdc3c7;
  display: inline-block;
  height: 2.5vh;
  width: 2.5vh;
  position: absolute;
  left: 5%;
  top: 10%;
}
.game-slider {
  height: 80%;
  width: 40%;
  background-color: #7f8c8d;
  position: absolute;
  top: 10%;
  border-radius: 2px;
}
.game-score {
  height: 40%;
  width: 30%;
  border-radius: 5px;
  position: absolute;
  top: 1vh;
  left: 35%;
  background-color: #7f8c8d;
  display: flex;
  align-items: center;
  justify-content: center;
}
.game-score span {
  height: 90%;
  width: 91.5%;
  color: #7f8c8d;
  font-family: Digital;
  background-color: #2c3e50;
  padding: 0.5vh;
  text-align: center;
  vertical-align: middle;
  font-size: 6vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
}
.game-score span.game-light-off {
  color: #7f8c8d;
}
.game-score span.game-light-on {
  color: #e74c3c;
}
.game-light-off {
  background-color: #e74c3c;
}
.game-light-on {
  background-color: #2ecc71;
}

